.page-header {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.page-header__logo {
  padding: 10px 19% 12px 6.1%;

  border-bottom: @border-light-grey;
}

.page-header__logo-image {
  width: 191px;
  height: 39px;
}

@media (min-width: @tablet-width) {
  .page-header {
    width: auto;

    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 25px;
    padding: 23px 3.9% 9px;
  }

  .page-header__logo {
    padding: 0;

    border: none;
  }

  .page-header__logo-image {
    width: 174px;
    height: 59px;
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    width: 1220px;
    margin: 0 auto;
    padding: 0;
    padding-top: 53px;
    padding-bottom: 55px;
    margin-bottom: 35px;

    &--index {
      &::before {
        content: "";

        position: absolute;
        top: 0;
        left: 50%;
        z-index: -1;

        width: 50vw;
        min-height: 696px;

        background-color: #65a93c;
        background-image: url("../img/index-background-desktop@1x.jpg");
        background-position: right;
        background-repeat: no-repeat;
        background-size: 720px auto;

        @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
          background-image: url("../img/index-background-desktop@2x.jpg");
        }
      }

      overflow: hidden;
    }
  }

  .page-header__logo-image {
    width: 203px;
    height: auto;
  }
}
