.social {
  padding: 22px 9px 16px 0;

  border-bottom: 1px solid rgba(17, 17, 17, 0.1);
}

.social__list {
  .list-reset();
  width: 45%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  list-style: none;
}

.social__icon {
  fill: @brownish-grey;
}

.social__link {
  &:hover .social__icon {
    fill: @bg-green;
  }

  &:active .social__icon {
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .social {
    border-bottom: none;
  }
}
